@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
    box-sizing: border-box;
}
html {
    height: 100%;
}
body {
    font-family: Helvetica, Arial, sans-serif;
    min-width: 320px;
    min-height: 100vh;
    margin: 0;
    height: 100%;
    width: 100%;
    color: #555;
    font-size: inherit;
    -webkit-font-smoothing: antialiased;
    background-color: #fff;
}
img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.wrapper {
    margin: 0 auto;
    max-width: 1170px;
    padding: 0 15px;
}

.reviewBannerIo {
    margin: 15px auto;
    display: flex;
    flex-direction: column;
    grid-row-gap: 10px;
    row-gap: 10px;
    align-items: center;
}
.reviewsBanner-text {
    color: #555;
    font-size: 12px;
    text-decoration: none;
    width: max-content;
    line-height: 12px;
    display: flex;
    align-items: center;
    grid-column-gap: 6px;
    column-gap: 6px;
}
.reviewsBanner-text span img{
    width: 79px;
    height: 13px;
}
strong {
    font-size: 14px;
}
.title-class {
    color: #555;
    font-size: 24px;
    margin: 0;
}
.confirm-button-class {
    margin: 0;
    background-color: #0f4c8b;
}


/* Media */
@media screen and (max-width:330px) {
    .reviewsBanner-text {
        font-size: 10px;
    }
    strong {
        font-size: 12px;
    }
}
.Header {
    background-color: #0f4c8b;
}
.logoBox {
    width: 114px;
    height: 35px;
}
.linkBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.how {
    font-size: 14px;
    line-height: 20px;
    color: white;
    text-decoration: none;
    padding: 15px 9px 13px;
    border-bottom: 2px solid #0f4c8b;
    border-left: 1px solid #0f4c8b;
    border-right: 1px solid #0f4c8b;
}
.how:hover {
    background: #0f4c8b !important;
    border-bottom: 2px solid #fb921e;
    color: #d3d3d3 !important;
}
.FormBig {
    max-width: 750px;
    margin: 0 auto;
    margin-top: 50px;
}
.Form {
    margin: 0 auto;
    max-width: 282.5px;
    text-align: center;
}
.error {
    border-radius: 7px;
    background-color: #f2dede;
    border-color: #ebccd1;
    color: #a94442;
    border: 1px solid #ebccd1;
    margin-bottom: 20px;
    padding: 15px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
}
.errorText {
    margin: 0;
}
.close {
    border: none;
    background-color: transparent;
    cursor: pointer;
    color: #a94442;
    font-size: 18px;
    padding: 0;
    font-size: 21px;
    font-weight: 700;
    line-height: 1;
    opacity: .2;
}
.close:focus, .close:hover {
    color: #000;
    cursor: pointer;
    filter: alpha(opacity = 50);
    opacity: .5;
    text-decoration: none;
}
.logintitle {
    margin: 0;
    margin-bottom: 20px;
    color: inherit;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
    font-size: 18px;
}
.righttext {
    margin: 0;
    text-align: right;
}
.forgotpass {
    width: max-content;
    text-decoration: none;
    color: #4c84b9;
    outline: 0;
    font-size: 14px;
}
.forgotpass:hover {
    text-decoration: underline;
}
#forms {
    margin-bottom: 15px;
}
.btnLogin {
    width: 100%;
    height: 50px;
    background-color: #259b24;
    border: 0;
    filter: none;
    line-height: 40px;
    padding: 0;
    border-radius: 7px;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
}
.btnLogin:hover {
    background-color: #0a8f08;
}



.inputBlock {
    position: relative;
    width: 100%;
}
.form-control {
    border: 1px solid #cacaca;
    font-size: 14px;
    font-weight: 400;
    height: 50px;
    line-height: 1.13125rem;
    padding: 6px 12px;
    width: 100%;
    border-radius: 7px;
    margin-bottom: 15px;
    color: #555;
}
.placeholder {
    position: absolute;
    left: 8px;
    top: 17px;
    font-size: 16px;
    font-weight: 400;
    color: #cacaca;
    transition: all 0.5s;
    transform-origin: 0% 0%;
    background: none;
    pointer-events: none;
    padding: 0 4px;
    -webkit-user-select: none;
            user-select: none;
}
.form-control:focus {
    outline: 1.2px solid #66afe9;
    border: 1.2px solid #66afe9;
}
.form-control:user-valid ~ .placeholder {
   transform: scale(0.8) translateY(-30px);
   background: #fff;
   color: #66afe9;
}
.form-control:focus ~ .placeholder {
   transform: scale(0.8) translateY(-30px);
   background: #fff;
   color: #66afe9;
}
